import React, { useState } from "react";
import myStyle from './Main.module.scss';
import BankAccount from './content/BankAccount';
import Report from './content/Report';
import Directions from './content/Directions';

function Main({imageSrc}) {
	const [activeSection, setActiveSection] = useState("Report"); // 기본 섹션을 "schedule"로 설정
	
	const handleClick = (section) => {
    console.log(`${section} clicked`);
    // Add navigation or functionality here based on section
  };
  return (
		<div className={myStyle.main}>
			<div className={myStyle.title}>
				<div className={myStyle.buttonGroup}>
					<button className={myStyle.customButton} onClick={() => setActiveSection("Report")} >모시는 <span className={myStyle.point}>글</span></button>
					{/* <span className={myStyle.separator}> / </span> */}
					<button className={myStyle.customButton} onClick={() => setActiveSection("Directions")}>오시는 <span className={myStyle.point}>길</span></button>
					{/* <span className={myStyle.separator}> / </span> */}
					{/* <button className={myStyle.customButton} onClick={() => setActiveSection("BankAccount")}>마음전달하기</button> */}
					<button className={myStyle.customButton} onClick={() => setActiveSection("BankAccount")}>전하는 <span className={myStyle.point}>곳</span></button>
				</div>
			</div>
			<div className={myStyle.content}>
				{/*  화면 출력 */}
				{activeSection === "Report" && <Report imageSrc={imageSrc}/>}
				{activeSection === "Directions" && <Directions/>}
				{activeSection === "BankAccount" && <BankAccount />}
			</div>
    </div>
  );
}

export default Main;
